body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


table {
  margin: 2rem;
  width: 100%;
  color: #3c3c3c;
}

tr,
td {
  padding: 1rem;
  margin: 1rem;
  text-align: center;
}
td {
  width: 33%;
}

.Contacts_delete {
  font-size: inherit;
  background-color: red;
  color: #fff;
  border: 1px solid #fff;
  padding: 10px 15px;
  cursor: pointer;
}

.header {
  background-color: #3f3f3f;
  color: dodgerblue;
  font-size: 1.5rem;
  padding: 1rem;
  text-align: center;
}

.Main_container {
  margin: 1rem;
  padding: 1rem;
  font-size: 1.3rem;
}

.Main_add {
  display: inline-block;
  background-color: green;
  color: #fff;
  cursor: pointer;
  border: 1px solid #fff;
  padding: 10px 15px;
  text-decoration: none;
}

.AddContact_container {
  padding: 1rem;
  margin: 1rem;
  height: 100%;
  font-size: 1.2rem;
}
.form_link {
  text-decoration: none;
  font-size: inherit;
  color: #3c3c3c;
}
.AddContact_form {
  background-color: #ddd;
  margin: 1rem;
  padding: 1rem;
}
.AddContact_form label {
  display: block;
  font-size: 1.3rem;
  color: #3c3c3c;
  margin-bottom: 5px;
}
.AddContact_form input {
  width: 30%;
  padding: 5px;
  font-size: inherit;
}
.form_control {
  margin: 1rem 0;
}
.form_display {
  margin: 1rem 0;
}
.form_add {
  display: inline-block;
  background-color: green;
  color: #fff;
  cursor: pointer;
  border: 1px solid #fff;
  padding: 10px 15px;
  text-decoration: none;
  font-size: inherit;
}

.form_error {
  color: red;
}

